export default {
	data() {
		var one=10;
		var two=5;
		var three=66;
		var four=100;
		return {
			avatar:'',
			nickname:'',
			storageData: {},
			headers:[{
				"shang":one,
				"xia":'文章'
			},{
				"shang":two,
				"xia":'关注'
			},
			{
				"shang":three,
				"xia":'消息'
			},
			{
				"shang":four,
				"xia":'积分'
			}
			],
			contents:[{
				"wenzhang":'简阅会员'
			},
			{
				"wenzhang":'简阅活动'
			},
			{
				"wenzhang":'简东西'
			},
			{
				"wenzhang":'我的钱包'
			},
			{
				"wenzhang":'我的专题/文集',
			},
			{
				"wenzhang":'浏览历史'	
			}
			],
			heads:[
				{
				"shang":require('../../static/文章.png'),
				"xia":'我的文章'
			},{
				"shang":require('../../static/书架.png'),
				"xia":'我的书架'
			},
			{
				"shang":require('../../static/收藏.png'),
				"xia":'赞和收藏'
			},
			{
				"shang":require('../../static/任务.png'),
				"xia":'有奖任务'
			}
			]
			
		};
	},
	onLoad: function() {},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			// console.log(loginKey);
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar
			};
		} else {
			this.storageData = {
				login: false
			};
		}
		uni.request({
			url: 'http://localhost:8080/api/user/' + uni.getStorageSync('login_key').userId,
			method: 'GET',
			header: { 'content-type': 'application/json' },
			success: res => {
				if (res.data.code === 0) {
					console.log(res.data.data.avatar+'————————————');
					_this.avatar = res.data.data.avatar;
					_this.nickname = res.data.data.nickname;
				}
			}
		});
	},
	methods: {
	}
};
